body {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.level-one {
  display: flex;
  width: 100vw;
  height: 100vh;
}
.level-one-left {
  width: 50%;
  position: relative;
  background: url(/pics/tmk.jpg);
  background-size: cover;
  background-position: 90% 50%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  transition: all 2s ease-in-out;
  z-index: 1;
}
.level-one-left--openLeft {
  z-index: 10;
}
.level-one-left--openLeft .level-one-panel {
  right: 100%;
}
.level-one-left--openRight {
  left: -50%;
}
.level-one-left--openRight .level-one-panel {
  right: 100%;
}
.level-one-right {
  width: 50%;
  position: relative;
  background: url(/pics/stz.jpg);
  background-size: cover;
  background-position: -50% 50%;
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
  transition: all 2s ease-in-out;
}
.level-one-right--openRight {
  right: 50%;
  background-position: -100% 50%;
  z-index: 10;
}
.level-one-right--openRight .level-one-panel {
  right: -180%;
}
.level-one-right--openLeft {
  right: 50%;
}
.level-one-right--openLeft .level-one-panel {
  right: 100%;
}
.level-two {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: -100%;
  transition: right 2s ease-in-out;
  z-index: 2;
}
.level-two--open {
  right: -50%;
}
.level-two--hidden {
  right: 0%;
}
.level-three {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: -100%;
  transition: right 2s ease-in-out;
  z-index: 3;
}
.level-three--open {
  right: -50%;
}
.level-two-left {
  width: 50%;
}
.level-two-right {
  width: 50%;
}
.level-two-panel {
  padding: 100px 80px;
  background: #eeeeee;
  height: 100vh;
}
.level-one-panel {
  padding: 0px 30px;
  width: 615px;
  background: #eeeeee;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  transition: all 2s ease-in-out;
  overflow: hidden;
}
.level-one__logo--tmk {
  margin: 100px 0 80px;
}
.level-one__logo--sinz {
  margin: 300px 0 40px;
}
.level-one__title {
  font-size: 60px;
  line-height: 72px;
  margin-bottom: 90px;
}
.level-one__more {
  font-family: 'Europe';
  background: #f36e21;
  padding: 34px 100px 32px 40px;
  width: 315px;
  color: #fff;
  font-size: 24px;
  border-radius: 50px;
  position: relative;
  cursor: pointer;
}
.level-one__more:after {
  background: #eee;
  background: linear-gradient(to left, #eeeeee 0%, rgba(238, 238, 238, 0) 100%);
  content: "";
  height: 175px;
  left: -90px;
  opacity: 0.2;
  position: absolute;
  top: -50px;
  width: 50px;
  -webkit-transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transform: rotate(35deg);
  -ms-transform: rotate(35deg);
  transform: rotate(35deg);
  z-index: 1;
  animation: move 5s infinite ease-in-out 5s;
}
@keyframes move {
  from {
    left: -90px;
  }
  25% {
    left: 100%;
  }
  to {
    left: 120%;
  }
}
.level-one__more-arrow {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  height: 70px;
  width: 70px;
  background: #f58b4d;
}
.level-one__more-arrow:before {
  content: "";
  background: url(/pics/arrow.png);
  width: 30px;
  height: 20px;
  position: absolute;
  top: 25px;
  left: 21px;
}
.level-one__qr {
  margin-top: 40px;
}
.content h1 {
  font-size: 50px;
  text-align: center;
  margin-bottom: 40px;
}
.content-block {
  margin-bottom: 40px;
}
.content-back {
  font-family: 'Europe';
  border: 2px solid #f36e21;
  padding: 34px 60px 32px 100px;
  width: 315px;
  color: #f36e21;
  font-size: 24px;
  border-radius: 50px;
  position: relative;
  cursor: pointer;
  text-align: center;
}
.content-back--arrow {
  position: absolute;
  top: 10px;
  left: 10px;
  border-radius: 50%;
  height: 70px;
  width: 70px;
  background: #f36e21;
}
.content-back--arrow:before {
  content: "";
  background: url(/pics/arrow.png);
  transform: rotate(180deg);
  width: 30px;
  height: 20px;
  position: absolute;
  top: 25px;
  left: 21px;
}
